
import { useClickOutside } from "@/compositionFunctions/clickEvents";
import { useKeyDown } from "@/compositionFunctions/keyboardEvents";
import { computed, defineComponent, ref, watch, watchEffect } from "vue";

export default defineComponent({
  props: {
    placement: {
      type: String,
      default: "right",
      validator: (value: string) => ["right", "left"].indexOf(value) !== -1,
    },
    disabled: {
      type: Boolean,
      default: false,
      required: false,
    },
    full: {
      required: false,
      default: false,
      type: Boolean,
    },
    hover: {
      type: Boolean,
      default: false,
      required: false,
    },
    animate: {
      type: Boolean,
      default: false,
      required: false,
    },
  },
  setup(props) {
    const open = ref(false);

    // is menu open
    const isOpen = computed(() => {
      return open.value && !props.disabled;
    });

    // handle escape key
    const onEscape = (e) => {
      if (e.key === "Esc" || e.key === "Escape") {
        open.value = false;
      }
    };
    useKeyDown(onEscape);

    // handle clickoutside
    const {
      clickedOutside,
      elementRef: menuRef,
      registerEvent,
      unRegisterEvent,
    } = useClickOutside();
    watch(clickedOutside, (value) => {
      console.log("watch clickoutside", value);
      if (value) {
        open.value = false;
      }
    });
    watchEffect(() => {
      if (open.value) {
        registerEvent();
      } else {
        unRegisterEvent();
      }
    });

    const openClose = (value = null as any) => {
      if (!props.disabled) {
        open.value = value !== null ? value : !open.value;
      }
    };

    const animatedOpened = computed(() => {
      return props.animate && isOpen.value;
    });

    const animatedClosed = computed(() => {
      return props.animate && !isOpen.value;
    });

    const notAnimatedOpened = computed(() => {
      return !props.animate && isOpen.value;
    });

    return {
      menuRef,
      openClose,
      isOpen,
      animatedOpened,
      animatedClosed,
      notAnimatedOpened
    };
  },
});
